<template>
  <el-form-item :label="label">
    <el-input
      v-model="modelValue"
      :placeholder="'请输入' + label"
      clearable
      @keyup.enter="search(1)"
      @input="(v) => $emit('update:modelValue', v)"
      @clear="$emit('update:modelValue', '')"
    />
  </el-form-item>
</template>
<script>
export default {
  name: 'SearchInput',
  emits: ['update:modelValue'],
  props: {
    modelValue: {
      required: true,
    },
    label: {
      type: String,
      required: true,
    },
    search: {
      type: Function,
      required: true,
    },
  },
}
</script>
